document.addEventListener('DOMContentLoaded', function() {

	let menu = document.querySelector('.header_ul');
	let btn = document.querySelector('.header_menu_btn');
	let menuLinks = document.querySelectorAll('.header_link');

	btn.addEventListener('click', function() {
		this.classList.toggle('header_menu_btn_active');
		menu.classList.toggle('header_ul_active');
	});


	menuLinks.forEach(function(item) {
		item.onclick = function() {
			btn.classList.toggle('header_menu_btn_active');
			menu.classList.toggle('header_ul_active');
		}
	});

	

});